<template>
  <div>
    <hr />
    <div>
      <h5>  {{ comments.length }}</h5>
    </div>
    <!--     -->
    <div v-for="(comment, index) in comments" :key="comment.commentno">
      <b-form-textarea
        class="mt-2"
        id="textarea-rows"
        rows="3"
        size="lg"
        :disabled="registFlag"
        :value="comments[index].content"
      ></b-form-textarea>

      <div
        align="right"
        v-if="comment.userid === $store.state.auth.user.username"
      >
        <b-button
          type="submit"
          variant="outline-danger"
          size="md"
          @click="onDelete($event, comment)"
          class="btn_notice mt-3 ml-3"
          ></b-button
        >
      </div>
    </div>
    <hr />
    <!--   -->
    <div>
      <b-form-textarea
        class="mt-2"
        v-model="item.content"
        id="textarea-rows"
        placeholder="  "
        rows="3"
        size="lg"
        ref="content"
      ></b-form-textarea>

      <div align="right">
        <b-button
          variant="outline-dark"
          size="md"
          type="submit"
          class="btn_notice mt-3"
          v-on:click="onSubmit"
          ></b-button
        >

        <b-button
          type="reset"
          variant="outline-danger"
          size="md"
          v-on:click="onReset"
          class="btn_notice mt-3 ml-3"
          > </b-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { listAnswers, writeAnswer, deleteAnswer } from "@/api/answer.js";

export default {
  name: "noticeAnswers",
  props: {
    articleno: { type: Number },
  },
  computed: {},
  data() {
    return {
      comments: [],
      item: {
        articleno: 0,
        userid: "", // ** userid  
        content: "",
      },
      registFlag: this.registFlagValue(),
    };
  },
  created() {
    this.item.articleno = this.articleno;
    this.item.userid = this.$store.state.auth.user.username;
    listAnswers(
      this.articleno,
      (response) => {
        this.comments = response.data;
      },
      (error) => {
        console.log(error);
      }
    );
  },
  methods: {
    onSubmit(event) {
      //  
      event.preventDefault();
      let err = true;
      let msg = "";
      err &&
        !this.item.content &&
        ((msg = " "),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      else {
        this.registAnswer();
      }
    },
    onReset(event) {
      event.preventDefault();
      // Reset our form values
      this.item.subject = "";
      this.item.content = "";
    },
    onDelete(event, comment) {
      event.preventDefault();
      console.log("commentno", comment);
      // Reset our form values
      deleteAnswer(
        comment.commentno,
        ({ data }) => {
          console.log(data);
          alert(" .");
        },
        (error) => {
          console.log(error);
          alert("   .");
        }
      );
      this.$router.go(-1);
    },
    registAnswer() {
      console.log(this.item);
      writeAnswer(
        this.item,
        ({ data }) => {
          let msg = "   .";
          if (data === "SUCCESS") {
            msg = " .";
          }
          alert(msg);
        },
        (error) => {
          console.log(error);
          alert(" .   .");
        }
      );
      this.$router.go(-1);
    },
    registFlagValue() {
      return "disabled";
    },
  },
};
</script>

<style scoped>
textarea:disabled {
  background: #fbfbf7;
}
</style>
